import React from 'react';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';

import UniLogo from '../assets/SydUniLogo.png';
import './HomeView.scss';
import OverridesClassNames from './OverridesClassNames';
import OverridesInlineStyle from './OverridesInlineStyle';

const style = {
  homepage: {
    paddingTop: '20%',
    textAlign: 'center',
  },
  picture: {
    width: '150px',
    height: '170px',
    align: 'left',
    margin: 'auto',
  },
  title: {
    color: '#fff',
    textShadow: '1px 1px 5px rgba(0,0,0,0.5)',
    margin: '20px',
  },
};

export const HomeView = () => (
  <div style={style.homepage}>
    <img alt="" src={UniLogo} style={style.picture} />
    <h2 style={style.title}>Welcome To USYD Capstone Project - IoT Blockchain Group.2!</h2>
    <OverridesInlineStyle />
  </div>
);

export default HomeView;



