import React, { Component } from 'react';
import { IndexLink, Link } from 'react-router'
import PropTypes from 'prop-types'
// import './PageLayout.scss'
import { connect } from 'react-redux';
import compose from 'recompose/compose';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import AccountCircle from 'material-ui-icons/AccountCircle';
import withWidth, { isWidthUp } from 'material-ui/utils/withWidth';
import MenuIcon from 'material-ui-icons/Menu';
import AppDrawer from '../../components/AppDrawer';
import AppContent from '../../components/AppContent';
import GitLabLogo from '../../components/assets/img/gitlab-logo.svg';
import AppBg from '../../components/assets/img/app_bg.png';

//App layout styles
const styles = theme => ({
  '@global': {
    html: {
      boxSizing: 'border-box',
    },
    '*, *:before, *:after': {
      boxSizing: 'inherit',
    },
    body: {
      margin: 0,
      // background: theme.palette.background.default,
      backgroundImage: `url(${AppBg})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      color: theme.palette.text.primary,
      lineHeight: '1.2',
      overflowX: 'hidden',
      WebkitFontSmoothing: 'antialiased', // Antialiasing.
      MozOsxFontSmoothing: 'grayscale', // Antialiasing.
    },
  },
  root: {
    display: 'flex',
    alignItems: 'stretch',
    minHeight: '100vh',
    width: '100%',
  },
  rootHome: {
    // backgroundColor: theme.palette.primary[600],
    background: 'linear-gradient(45deg, #1565C0 30%, #1E88E5 90%)', 
    color: '#fff',   
  },
  grow: {
    flex: '1 1 auto',
  },
  title: {
    marginLeft: 24,
    flex: '0 1 auto',
  },
  appBar: {
    transition: theme.transitions.create('width'),
    backgroundColor: theme.palette.primary[600],
  },
  appBarHome: {
    backgroundColor: 'transparent',
    boxShadow: 'none',
    // display: 'none',
  },
  [theme.breakpoints.up('lg')]: {
    drawer: {
      width: '250px',
    },
    appBarShift: {
      width: 'calc(100% - 250px)',
    },
    navIconHide: {
      display: 'none',
    },
  },
});

function getTitle(routes) {
  for (let i = routes.length - 1; i >= 0; i -= 1) {
    if (routes[i].hasOwnProperty('title')) {
      return routes[i].title;
    }
  }

  return null;
}

class PageLayout extends Component {
  constructor(props){
    super(props);
    this.state = {
      drawerOpen: false,
    };
  }

  //doesn't need to bind if using this ES6 grammar: func_name = () => {...}
  handleDrawerClose = () => {
    this.setState({ drawerOpen: false });
  };

  handleDrawerToggle = () => {
    this.setState({ drawerOpen: !this.state.drawerOpen });
  };

  render() {
    const { children, routes, width } = this.props;
    
    const classes = this.props.classes;
    const title = getTitle(routes);

    let drawerDocked = isWidthUp('lg', width);
    let navIconClassName = '';
    let appBarClassName = classes.appBar;
    let rootClassName = classes.root;

    if (title === null) {
      // home route, don't shift app bar or dock drawer
      drawerDocked = false;
      appBarClassName += ` ${classes.appBarHome}`;
      rootClassName += ` ${classes.rootHome}`;
    } else if ( title === 'Login' || title === 'SignUp') {
      drawerDocked = false;
    } 
    else {
      navIconClassName += ` ${classes.navIconHide}`;
      appBarClassName += ` ${classes.appBarShift}`;
    }
    return (
      <div className={rootClassName}>

{/* -----------------AppBar---------------------- */}
        <AppBar className={appBarClassName}>
          <Toolbar>
            <IconButton
              color="contrast"
              aria-label="open drawer"
              onClick={this.handleDrawerToggle}
              className={navIconClassName}
            >
              <MenuIcon />
            </IconButton>
            {title !== null &&
              <Typography className={classes.title} type="title" color="inherit" noWrap>
                {title}
              </Typography>}
            <div className={classes.grow} />
            <IconButton
              title="Log In"
              component={Link}
              to='/login'
              color="contrast"
              onClick={this.handleToggleShade}
            >
                <AccountCircle />
            </IconButton>
            <IconButton
              title="View on Gitlab"
              color="contrast"
              onClick={this.handleToggleShade}
            >
              <a href='https://gitlab.com/tbsx3_capstone/capstone-project-group2' target='_blank'>
                <img src={GitLabLogo} width='20rem'/>              
              </a>
            </IconButton>
          </Toolbar>
        </AppBar>
{/* -----------------AppBar---------------------- */}

        <AppDrawer
          className={classes.drawer}
          docked={drawerDocked}
          routes={routes}
          onRequestClose={this.handleDrawerClose}
          open={drawerDocked || this.state.drawerOpen}
        />
        <AppContent>
          {children}
        </AppContent>
      </div>
    );
  }
}

PageLayout.propTypes = {
  children: PropTypes.node.isRequired,
  classes: PropTypes.object.isRequired,
  dispatch: PropTypes.func.isRequired,
  routes: PropTypes.array.isRequired,
  width: PropTypes.string.isRequired,
}

// export default PageLayout
export default compose(
  withStyles(createStyleSheet(styles), {
    name: 'PageLayout',
  }),
  withWidth(),
  connect(),
)(PageLayout);



