// We only need to import the modules necessary for initial render
import CoreLayout from '../layouts/PageLayout/PageLayout';
import Home from './Home';

/*  Note: Instead of using JSX, we recommend using react-router
    PlainRoute objects to build route definitions.   */

export const createRoutes = (store) => ({
  path        : '/',
  component   : CoreLayout,
  indexRoute  : Home,
  getChildRoutes (location, next) {
    require.ensure([], (require) => {
      next(null, [
        // Remove imports!
        require('./Counter').default(store),
        require('./Login').default(store),
        require('./SignUp').default(store),
        require('./Profile').default(store),
        require('./Products').default(store),
        require('./ProductDetail').default(store),
        require('./Orders').default(store),
        require('./OrderDetail').default(store),
      ])
    })
  }
})

export default createRoutes;




